.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 80px;
    margin-bottom: 16px;
    /* border-bottom: 1px solid #2a363b; */
    box-shadow: 0px 4px 4px rgb(5, 5, 5);
}

.link {
    display: inline-block;
    text-decoration: none;
    padding: 12px;
    font-weight: 500;
    color: #2a363b;
    font-size: 18px;
}

.activeLink {
    composes: link;
    color: #2196f3;
}